.liveUrlBtn{
  margin:0;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 5px;
  color:#fff;
  padding: 5px 20px;
  width: 100%;
  transition: all 0.3s ease-in-out;

  &:hover{
    border: 1px solid transparent;
    background-color: #fff;
    color: #000;
  }
}